/* ==========================================================================
   Video Button shortcode start styles
   ========================================================================== */

.edgtf-video-button-holder {
	position: relative;
	display: inline-block;
	vertical-align: middle;

	&:hover {

		.edgtf-video-button-image {

			&:after {
				opacity: 1;
			}

			img {
				@include edgtfTransform(scale(1.02) translateZ(0));
				@include edgtfTransitionTransform(.25s cubic-bezier(.21,.6,.44,2.18));
			}
		}
	}
	
	&.edgtf-vb-has-img {
		
		.edgtf-video-button-play,
		.edgtf-video-button-play-image {
			@include edgtfAbsoluteHolderLayout();
			z-index: 1;
			
			.edgtf-video-button-play-inner {
				position: relative;
				top: 50%;
				left: 0;
				display: block;
				text-align: center;
				@include edgtfTransform(translateY(-50%));
			}
		}
	}
	
	.edgtf-video-button-image {
		@include edgtfRelativeHolderLayout();
		overflow: hidden;

		&:after {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0,0,0,.25);
			opacity: 0;
			@include edgtfTransition(opacity .3s ease);
		}
		
		img {
			display: block;
			@include edgtfTransform(scale(1) translateZ(0));
			@include edgtfTransitionTransform(.25s ease);
		}
	}
	
	.edgtf-video-button-play,
	.edgtf-video-button-play-image {
		position: relative;
		display: inline-block;
		vertical-align: top;
		z-index: 1;
	}
	
	.edgtf-video-button-play {
		color: $default-heading-color;
		font-size: 106px;
		line-height: 1;
		
		span {
			display: block;
			line-height: inherit;
			
			&:before {
				display: block;
				line-height: inherit;
			}
		}
	}
	
	.edgtf-video-button-play-image {
		
		&.edgtf-vb-has-hover-image {
			
			&:hover {
				
				img {
					
					&:first-child {
						opacity: 0;
					}
					
					&:nth-child(2) {
						opacity: 1;
					}
				}
			}
		}
		
		img {
			display: block;
			margin: 0 auto;
			@include edgtfTransition(opacity .3s ease-in-out);
			
			&:first-child {
				position: relative;
				opacity: 1;
			}
			
			&:nth-child(2) {
				position: absolute;
				top: 0;
				left: 50%;
				opacity: 0;
				@include edgtfTransform(translateX(-50%) translateZ(0));
				
				.rev_slider_wrapper & {
					@include edgtfTransform(translateZ(0));
				}
			}
		}
	}
}
/* ==========================================================================
   Video Button shortcode end styles
   ========================================================================== */